import Web3 from 'web3'
import { BigNumber } from 'bignumber.js'
import { toBN, BN, isBN } from 'web3-utils'

const dood_abi = require('./abis/dood.json')

const web3Provider = 'https://data-seed-prebsc-2-s3.binance.org:8545/'
const web3Address = '0x84dd30f6b952bd02249b07645c47d60689f639ae'

export class Dood {
	constructor() {
		this.web3 = new Web3(window.ethereum)
		this.web3Reader = new Web3(new Web3.providers.HttpProvider(web3Provider))

		this.contractReader = new this.web3Reader.eth.Contract(
			dood_abi,
			web3Address
		)

		if (this.web3 != null) {
			this.contract = new this.web3.eth.Contract(
				dood_abi,
				web3Address
			)
		}
	}

	async totalSupply() {
		return this.contractReader.methods.totalSupply().call()
	}

	async isActive() {
		return this.contractReader.methods.isActive().call()
	}

	async freeMintCount() {
		return this.contractReader.methods.freeMintCount().call()
	}

	async mint(account, count, callback) {
		var gasPrice = 21000000000

		let weiAmount = BigNumber(count * 35).times(BigNumber(10).pow(BigNumber(15)));
		let weiAmountBN = toBN(weiAmount)

		//console.log(this.web3Reader.toWei('0.035', 'ether'))

		var tx = this.contract.methods.mint(count)
		var gasLimit = await tx.estimateGas({ value: weiAmountBN, from: account, to: web3Address})
		return tx.send({
			from: account,
			value: weiAmountBN,
			gasPrice: gasPrice,
			gas: Math.round(gasLimit * 1.1)
		}, callback)
	}

}

export default Dood