import Vue from 'vue'
import Dood from '../contracts/dood.js'

const web3 = {
	state: {
		account: '',
		balance: 0,
		doodReader: null,
		dood: null
	},
	mutations: {
		UPDATE_ACCOUNT: (state, account) => {
			console.log('update account')
			state.account = account
		},
		UPDATE_BALANCE: (state, balance) => {
			state.balance = balance
		},
		INIT_DOOD: (state, dood) => {
			state.dood = dood
		},
		INIT_DOOD_READER: (state, doodReader) => {
			state.doodReader = doodReader
		}
	},

	actions: {
		updateAccount({ commit }, account) {
			commit('UPDATE_ACCOUNT', account)

			let dood = new Dood()
			commit('INIT_DOOD', dood)
		},
		updateBalance({ commit }, balance) {
			commit('UPDATE_BALANCE', balance)
		},
		async init({ commit, dispatch}) {
			let doodReader = new Dood()

			commit('INIT_DOOD_READER', doodReader)

			console.log('init')
			//if (window.ethereum) {
			//	commit('INIT_DOOD', doodReader)
			//}
		}
	}
}

export default web3